/*For the content*/

/* Course Table Of Contents */
.chapter_square {
  background: linear-gradient(
    90deg,
    rgba(6, 85, 194, 1) 0%,
    rgba(0, 165, 255, 1) 100%
  );
  margin-top: 5px;
  padding: 5px;
  position: relative;
  border-radius: 5px;
  -webkit-appearance: none;
}
.chapter_number {
  background: white;
  border-radius: 100%;
  border-width: 20px;
  color: #006ae1;
  display: inline-block;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.1em;
  padding: 2px 4px 2px 4px;
  -webkit-appearance: none;
}
.chapter_title {
  padding-left: 5px;
  text-decoration: none;
  color: #ffffff;
  display: inline-block;
  margin: 0px;
  font-size: 20px;
  font-weight: 700;
  -webkit-appearance: none;
}

.chapter_topic_list ul {
  padding: 0.5em;
  list-style-type: none;
}
.chapter_topic_list ul li {
  position: relative;
  padding: 0.5em 1em 0.5em 2em;
  margin-bottom: 5px;
  border-bottom: 1px solid #7bb2ff;
  color: #006ae1;
  font-weight: bold;
}

.chapter_topic_list ul li:before {
  content: "";
  position: absolute;
  border-radius: 50%;
}
.chapter_topic_list ul li:before {
  top: 50%;
  left: 0.2em;
  width: 17px;
  height: 17px;
  background: rgba(0, 106, 225, 0.8);
  transform: translateY(-50%);
}
.contentTwo .chapter_topic_list ul li:before {
  top: 50%;
  left: 0.2em;
  width: 17px;
  height: 17px;
  background: rgba(255, 255, 255, 0.8);
  transform: translateY(-50%);
}

/* Key Point List (number in circle) */
.key_point_list {
  margin: 1rem 0 1rem 0;
  -webkit-appearance: none;
}
.key_point_list_title_normal {
  display: inline-block;
  padding: 1px 10px 0 10px;
  color: #006ae1;
  letter-spacing: 0.1em;
  font-size: 1.2rem;
  font-weight: 900;
  -webkit-appearance: none;
}
.key_point_list_title_tab {
  display: inline-block;
  padding: 1px 10px 0 10px;
  background: #006ae1;
  color: #fff;
  letter-spacing: 0.1em;
  font-weight: bold;
  border-radius: 5px 5px 0px 0px;
  -webkit-appearance: none;
}
.key_point_list ul,
.key_point_list ol {
  margin: 0;
  padding: 5px;
  position: relative;
  border: solid 2px #006ae1;
  background: #fff;
  -webkit-appearance: none;
}
.key_point_list ol {
  counter-reset: number;
  -webkit-appearance: none;
}
.key_point_list ul li,
.key_point_list ol li {
  color: #006ae1;
  line-height: 1.5;
  padding: 0.5em 0.5em 0.5em 1.8em;
  list-style-type: none;
  -webkit-appearance: none;
}

.key_point_list ul li:before {
  display: inline-block;
  position: absolute;
  left: 0.5em;
  width: 12px;
  margin-top: 5px;
  height: 12px;
  border-radius: 0%;
  /* background-color: white; */
  filter: drop-shadow(1px 1px 1px #7bb2ff);
  border-width: 2px;
  border-color: #006ae1;
  color: #006ae1;
  font-family: "Quicksand", sans-serif;
  font-size: 12px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  line-height: 12px;
  content: "";
  -webkit-appearance: none;
}
.key_point_list ol li:before {
  display: inline-block;
  position: absolute;
  left: 0.5em;
  width: 22px;
  height: 22px;
  border-radius: 100%;
  background: #006ae1;
  color: white;
  font-family: "Quicksand", sans-serif;
  font-size: 12px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  line-height: 22px;
  content: counter(number);
  counter-increment: number;
  -webkit-appearance: none;
}

h3 {
  padding-left: 5px;
  padding-right: 10px;
  margin-bottom: -16px;
  line-height: 1.3rem;
  color: #006ae1;
